/**
 * ArcgisChartsError is a class that enhances the debugging and error handling process. Rather than returning a generic [JavaScript
 * error](https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Error),
 * this ArcgisChartsError returns a standardized error object with several properties.
 */
export class ArcgisChartsError extends Error {
    /**
     * Creates a new ArcgisChartsError instance.
     *
     * @param {string} name - A unique error name.
     * @param {string} [message] - A message describing the details of the error.
     * @param {Object} [details] - The details object provides additional details specific to the error.
     */
    constructor(name, message, details) {
        super(message);
        this.name = name;
        this.details = details;
    }
}
