export function getLayerSpatialAggregationStatisticsCapabilities(layer) {
    let compatibleWithEnvelopeAggregate = false;
    const supportedSpatialAggregationStatisticsJSON = layer.sourceJSON?.advancedQueryCapabilities?.supportedSpatialAggregationStatistics ?? [];
    const supportedSpatialAggregationStatistics = layer.capabilities?.query?.supportedSpatialStatisticAggregations;
    compatibleWithEnvelopeAggregate =
        supportedSpatialAggregationStatisticsJSON.includes("EnvelopeAggregate") ||
            supportedSpatialAggregationStatistics?.envelope === true;
    return { compatibleWithEnvelopeAggregate };
}
