import { RESTStatisticType } from "@arcgis/charts-spec";
import { queryAllFeatures, queryStatistics } from "../../query-features";
/**
 * Fetches all the unique values of a field from a feature layer under given extent and where clause
 * @param props
 */
export async function uniqueValues(props) {
    const { queryObject, queryConfig, field, abortKey } = props;
    const modifiedQueryConfig = { ...queryConfig, returnDistinctValues: true };
    // Counting the number of unique values for the given field
    const uniqueValuesCount = (await queryStatistics({
        queryObject,
        queryConfig: {
            ...modifiedQueryConfig,
            outStatistics: [
                {
                    statisticType: RESTStatisticType.Count,
                    onStatisticField: field,
                    outStatisticFieldName: RESTStatisticType.Count
                }
            ]
        },
        abortKey
    })).get(RESTStatisticType.Count);
    // Executing the series of queries to get unique values from the field
    const allFeatures = (await queryAllFeatures({
        queryObject,
        featureCount: uniqueValuesCount,
        queryConfig: modifiedQueryConfig,
        outFields: [field],
        abortKey
    }));
    // mapping the results to provide an array of unique values. Rejecting the undefined values in case
    // the query doesn't bring any result, or none having a value for the requested field.
    const uniqueValuesArray = allFeatures
        .map((feature) => feature.attributes[field])
        .filter((value) => value !== undefined);
    return uniqueValuesArray;
}
