/**
 * create a popover element based on the input parameters
 * @param popoverType popover type to be created
 * @param popoverConfig config used to configure popover
 * @returns popover element
 */
export const createPopover = (popoverType, popoverConfig) => {
    // declare a new popover
    let newPopover;
    switch (popoverType) {
        // Histogram data popover
        case PopoverType.HistogramDataPopover:
            newPopover = createHistogramDataPopover(popoverConfig);
            break;
        // Bar Chart data popover
        case PopoverType.BarChartDataPopover:
            newPopover = createBarChartDataPopover(popoverConfig);
            break;
        // * Bar Chart Series Popover
        case PopoverType.BarChartSeriesPopover:
            newPopover = createBarChartSeriesPopover(popoverConfig);
            break;
        // Box Plot data popover
        case PopoverType.BoxPlotDataPopover:
            newPopover = createBoxPlotDataPopover(popoverConfig);
            break;
        // * Box Plot Series Popover
        case PopoverType.BoxPlotSeriesPopover:
            newPopover = createBoxPlotSeriesPopover(popoverConfig);
            break;
        // * Line Chart data popover
        case PopoverType.LineChartDataPopover:
            newPopover = createConfigLineChartDataPopover(popoverConfig);
            break;
        // Pie Chart data popover
        case PopoverType.PieChartDataPopover:
            newPopover = createPieChartDataPopover(popoverConfig);
            break;
        // Format chart popover
        case PopoverType.FormatPopover:
            newPopover = createFormatPopover(popoverConfig);
            break;
        // Guide popover
        case PopoverType.GuidePopover:
            newPopover = createGuidePopover(popoverConfig);
            break;
        // Line Chart series popover
        case PopoverType.LineChartSeriesPopover:
            newPopover = createLineChartSeriesPopover(popoverConfig);
            break;
        // Pie Chart slices popover
        case PopoverType.PieChartSlicesPopover:
            newPopover = createPieChartSlicesPopover(popoverConfig);
            break;
        // Scatter Plot data popover
        case PopoverType.ScatterPlotDataPopover:
            newPopover = createScatterPlotDataPopover(popoverConfig);
            break;
        // Select Chart Type popover
        case PopoverType.SelectChartTypePopover:
            newPopover = createSelectChartTypePopover(popoverConfig);
            break;
        // Chart Rename Popover
        case PopoverType.ChartRenamePopover:
            newPopover = createChartRenamePopover(popoverConfig);
            break;
        default:
            throw new Error(`Invalid popover type provided`);
    }
    newPopover.open = true;
    newPopover.addEventListener("arcgisChartsConfigPopoverClose", popoverConfig.popoverClose);
    return newPopover;
};
/**
 * edit popover element dynamically
 * @param popover a popover element
 * @param isOpen determine whether to open popover or not
 */
export const editPopover = (popover, isOpen = false) => {
    if (isNil(popover))
        return;
    // style valid popover if it is open
    if (isOpen) {
        popover.style.setProperty("opacity", "1");
        popover.style.setProperty("pointer-events", "initial");
    }
    else {
        // remove styles from the popover
        popover.style.removeProperty("opacity");
        popover.style.removeProperty("pointer-events");
    }
    const refElement = popover.referenceElement;
    const vElement = refElement !== undefined
        ? {
            getBoundingClientRect: () => {
                return refElement.getBoundingClientRect();
            }
        }
        : {
            getBoundingClientRect: () => {
                return new DOMRect();
            }
        };
    popover.referenceElement = vElement;
    popover.triggerDisabled = isOpen;
    popover.open = isOpen;
    popover.autoClose = isOpen;
};
