// default color set from Pro
export const ramp = [
    [31, 120, 180, 255],
    [166, 206, 227, 255],
    [51, 160, 44, 255],
    [178, 223, 138, 255],
    [227, 26, 28, 255],
    [251, 154, 153, 255],
    [255, 127, 0, 255],
    [253, 191, 111, 255],
    [106, 61, 154, 255],
    [202, 178, 214, 255]
];
// default color set for Histogram Overlays
const overlayRamp = [
    [104, 104, 104, 255],
    [51, 124, 235, 255],
    [132, 0, 168, 255],
    [156, 156, 156, 255]
];
/**
 * Returns a specific color from the default ramp, given an index. If the index is > than the ramp length,
 * we use modulo to get the corresponding color.
 * @param props.colorIndex The index to fetch the color from the `ramp` or `overlayRamp` array
 * @param props.opacity The opacity to set the alpha value, set to 255 as default
 * @param props.histogramOverlays The boolean that is used to identify from which array to fetch default colors
 */
export function getOneRampColor(props) {
    const { colorIndex, opacity, histogramOverlays } = props;
    // if the color index is negative or missing, we default it to 0
    const index = Math.max(0, colorIndex ?? 0);
    // Use the overlayRamp[] to fetch default histogram overlay colors, else use ramp[] to get other default colors
    const rampToUse = [...(histogramOverlays === true ? overlayRamp : ramp)];
    const [r, g, b, a] = rampToUse[index % rampToUse.length];
    const alpha = opacity !== undefined ? opacity * 255 : a;
    return [r, g, b, alpha];
}
export function getDefaultColor(opacity) {
    return [0, 0, 0, (opacity ?? reducedOpacity) * 255];
}
/**
 * random color with alpha 70% for authoring page.
 * @param opacityPercentage default is 1
 */
export function generateRandomRestColor(opacityPercentage = 1) {
    return [
        Math.floor(Math.random() * 256),
        Math.floor(Math.random() * 256),
        Math.floor(Math.random() * 256),
        opacityPercentage * 255
    ];
}
export function getDefaultFont(type) {
    const font = {
        family: "Arial, Helvetica, sans-serif",
        size: 10,
        style: RESTFontStyle.Normal,
        weight: RESTFontWeight.Normal,
        decoration: RESTFontDecoration.None
    };
    switch (type) {
        case TextElements.ChartTitle:
            font.size = 18;
            break;
        case TextElements.AxisTitle:
            font.size = 14;
            break;
        case TextElements.LegendText:
            font.size = 12;
            break;
        case TextElements.LegendTitle:
            font.size = 13;
            break;
        case TextElements.DescriptionText:
            font.size = 11;
            break;
        default:
            break;
    }
    return font;
}
export function getDefaultTextSymbol(text = "", font = getDefaultFont(), type) {
    // default alignment for all labels is "center"
    let horizontalAlignment = RESTHorizontalAlignment.Center;
    // exception: for data labels and guide labels we let ac-js default the horizontal alignment
    if (type === TextElements.DataLabels || type === TextElements.GuideLabel)
        horizontalAlignment = undefined;
    return {
        type: RESTSymbolType.TS,
        color: getDefaultColor(),
        text,
        font,
        horizontalAlignment
    };
}
export function getDefaultChartText(props) {
    const { text, visible, font, type } = props ?? {
        text: "",
        visible: true,
        font: getDefaultFont()
    };
    return {
        type: WebChartTypes.Text,
        visible,
        content: getDefaultTextSymbol(text, font, type)
    };
}
export function getDefaultLineSymbol(props) {
    const useRandomColor = props?.useRandomColor ?? false;
    const useRampColor = props?.useRampColor;
    const width = props?.width ?? 1;
    const isHistogramOverlay = props?.isHistogramOverlay;
    let color = props?.color ?? getDefaultColor(1);
    if (useRandomColor === true)
        color = generateRandomRestColor();
    else if (useRampColor !== undefined) {
        if (isHistogramOverlay === true) {
            color = getOneRampColor({ colorIndex: useRampColor.colorIndex, histogramOverlays: isHistogramOverlay });
        }
        else
            color = getOneRampColor({ colorIndex: useRampColor.colorIndex });
    }
    if (props?.opacity !== undefined)
        color[3] = props.opacity;
    return {
        type: RESTSymbolType.SLS,
        style: RESTSimpleLineSymbolStyle.Solid,
        color,
        width
    };
}
