function getLayerIDFromUrl(url) {
    return url?.substring(url?.lastIndexOf("/") + 1);
}
export function getDefaultDataSource(dataSource) {
    const newDataSource = cloneDeep(dataSource);
    if (dataSource !== undefined && dataSource.type === WebChartDataSourceTypes.Layer) {
        // Setting other required properties of IFeatureLayer
        // Currently only one layer is supported for featureCollection so setting to 0 for featureCollection.
        // Group Layer not yet supported.
        newDataSource.layer = {
            ...newDataSource.layer,
            layerType: "ArcGISFeatureLayer",
            id: dataSource.layer.id ?? getLayerIDFromUrl(dataSource.layer?.url) ?? 0
        };
    }
    return newDataSource;
}
