/**
 * Returns a list of render limitations, mainly set for performance reasons. They can be overridden through the chart options,
 * or let as defaults.
 */
export function getChartLimits(customizedLimits) {
    const chartLimits = { ...defaultLimits };
    if (customizedLimits !== undefined) {
        Object.keys(defaultLimits).forEach((propertyName) => {
            if (propertyName === "behaviorAfterLimit")
                chartLimits[propertyName] = customizedLimits[propertyName] ?? defaultLimits[propertyName];
            else
                chartLimits[propertyName] = customizedLimits[propertyName] ?? defaultLimits[propertyName];
        });
    }
    return chartLimits;
}
/**
 * Returns a list of technical limitations, mainly to the size of large queries.
 */
export function getChartTechnicalLimits() {
    return {
        /* Specifies the size of the queries on an aggregated scatter plot, counting the number of cells per query.
        A bigger number implies fewer but slower queries. A smaller number implies more but faster queries.
        */
        maxScatterCellsPerQuery: 500,
        /* Specifies the size of the queries on an aggregated scatter plot, counting the number of cells per query for Hosted feature layers using standard databases in ArcGIS Online.
        A bigger number implies fewer but slower queries. A smaller number implies more but faster queries.
        */
        maxScatterCellsPerQueryForStandard: 175,
        /**
         * Specifies the number of elements (bars, markers, slices) to be considered per selection query.
         */
        maxElementsPerSelectionQuery: 25,
        /**
         * Specifies the maximum number of OIDs to be used in a selection query. They are used as `OBJECTID IN (<oid list>)`
         */
        maxOIDsPerSelectionQuery: 2000,
        /**
         * Specifies the maximum number of operands (e.g. field < value) in a where clause.
         */
        maxOperandsPerWhereClause: 200
    };
}
/**
 * Returns the label for the provided aggregation type.
 * @param aggregationType
 */
export async function getDefaultAggregationTypeLabel(aggregationType) {
    const commonStrings = await fetchCommonStrings();
    let title;
    switch (aggregationType) {
        case RESTStatisticType.Average: {
            title = commonStrings.statistics.mean;
            break;
        }
        case RESTStatisticType.ContinuousPercentile: {
            title = commonStrings.statistics.median;
            break;
        }
        case RESTStatisticType.Sum: {
            title = commonStrings.statistics.sum;
            break;
        }
        case RESTStatisticType.Minimum: {
            title = commonStrings.statistics.minimum;
            break;
        }
        case RESTStatisticType.Maximum: {
            title = commonStrings.statistics.maximum;
            break;
        }
        case RESTStatisticType.Variance: {
            title = commonStrings.statistics.variance;
            break;
        }
        case RESTStatisticType.DiscretePercentile: {
            title = commonStrings.statistics.discretePercentitle;
            break;
        }
        case RESTStatisticType.NoAggregation: {
            title = commonStrings.statistics.noAggregation;
            break;
        }
        case RESTStatisticType.Count:
        default: {
            title = commonStrings.statistics.count;
            break;
        }
    }
    return title;
}
/**
 * Returns the label for the provided time aggregation type.
 * @param aggregationType
 */
export async function getDefaultTimeAggregationTypeLabel(timeAggregationType) {
    const commonStrings = await fetchCommonStrings();
    let title;
    switch (timeAggregationType) {
        case WebChartTimeAggregationTypes.End: {
            title = commonStrings.timeAggregationTypes.end;
            break;
        }
        case WebChartTimeAggregationTypes.Start: {
            title = commonStrings.timeAggregationTypes.start;
            break;
        }
        default:
            title = "";
            break;
    }
    return title;
}
