import { WebChartDataTransformations } from "@arcgis/charts-spec";
import { median as d3Median, deviation as d3Deviation, mean as d3Mean, bin as d3Bin, range as d3Range, sum as d3Sum } from "d3-array";
import { DataWarningKeywords } from "../../chart-config/validator/interfaces";
import { histogramBinCount } from "../../defaults";
/**
 * Checks if the current transformation type (log | sqrt) is compatible with the dataset.
 * @param dataTransformationType The transformation requested for the histogram
 * @param min The min value from the data set
 */
export function isDataCompatibleWithTransformation(dataTransformationType, min) {
    return (dataTransformationType === WebChartDataTransformations.None ||
        (dataTransformationType === WebChartDataTransformations.Log && min > 0) ||
        (dataTransformationType === WebChartDataTransformations.Sqrt && min >= 0));
}
/**
 * Create the bins for the original min and max value (Before applying the transformation).
 * @param props
 */
export function createInvalidDataInfoForHistogram(dataTransformationType, dataCountIncompatible) {
    const invalidDataInfo = { valid: false };
    if (dataCountIncompatible === true)
        invalidDataInfo.invalidType = DataWarningKeywords.HistogramEmptyField;
    else if (dataTransformationType === WebChartDataTransformations.Log)
        invalidDataInfo.invalidType = DataWarningKeywords.NegativeValueInLogCalculation;
    else if (dataTransformationType === WebChartDataTransformations.Sqrt)
        invalidDataInfo.invalidType = DataWarningKeywords.NegativeValueInSqrtCalculation;
    return invalidDataInfo;
}
/**
 * Creates an object able to generate @binCount bins
 * @param binCount
 */
function createBinMaker(binCount) {
    return d3Bin().thresholds((_data, valmin, valmax) => d3Range(binCount).map((t) => valmin + (t / binCount) * (valmax - valmin)));
}
/**
 * Applies a log or sqrt to a value according to the requested transformation type
 * @param value
 * @param transformation
 */
export function transformValue(value, transformation) {
    let result = value;
    switch (transformation) {
        case WebChartDataTransformations.Log:
            result = Math.log(value); // use natural log
            break;
        case WebChartDataTransformations.Sqrt:
            result = Math.sqrt(value);
            break;
        default:
            break;
    }
    return result;
}
/**
 * Applies the required transformation on the data and fetches a few stats.
 * @param props
 */
function applyTransformation(props) {
    const { field, data, transformation } = props;
    let nonNullCount = 0;
    let min = Number.MAX_SAFE_INTEGER;
    const transformedData = [...data];
    for (let idx = 0; idx < transformedData.length; idx += 1) {
        let value = transformedData[idx][field];
        // If value is null, no transformation is applied
        if (typeof value === "number") {
            min = Math.min(min, value);
            value = transformValue(value, transformation);
            // Assigning the new value to the array
            transformedData[idx][field] = value;
            nonNullCount += 1;
        }
    }
    return { transformedData, nonNullCount, min };
}
/**
 * Applies the requested transformation on raw data for histogram, and returns the transformed data
 * along with some metadata
 * @param data The raw data represented by an array of POJOs
 * @param seriesConfig The histogram series from the config
 * @returns
 */
export function fetchInlineRawHistogramData(data, seriesConfig) {
    const { x, dataTransformationType } = seriesConfig;
    const countIncludingNulls = data.length;
    const { transformedData: rawData, nonNullCount: countExcludingNulls, min } = applyTransformation({
        field: x,
        data,
        transformation: dataTransformationType
    });
    const dataCountIncompatible = countExcludingNulls < 2;
    const dataCompatibleWithTransformation = isDataCompatibleWithTransformation(dataTransformationType, min);
    return {
        rawData,
        dataCompatibleWithTransformation,
        dataCountIncompatible,
        countExcludingNulls,
        countIncludingNulls
    };
}
/**
 * Transforms transformed raw data into histogram data (bins + stats).
 * @param seriesConfig The histogram series from the config
 * @param rawData The transformed data
 * @returns
 */
export function transformInlineHistogramData(seriesConfig, rawData) {
    const { x, binCount } = seriesConfig;
    const transformResult = { valid: true };
    if (rawData !== undefined) {
        // Reducing the array to the values of the x field
        const numericData = rawData.map((data) => data[x]);
        // Creating the bins and formatting them the right way
        let bins = [];
        bins = createBinMaker(binCount ?? histogramBinCount)(numericData).map((bin) => {
            return {
                count: bin.length,
                minValue: bin.x0 ?? NaN,
                maxValue: bin.x1 ?? NaN
            };
        });
        // Calculating the stats
        const mean = d3Mean(numericData) ?? NaN;
        const stddev = d3Deviation(numericData) ?? NaN;
        const median = d3Median(numericData) ?? NaN;
        const min = bins[0].minValue;
        const max = bins[bins.length - 1].maxValue;
        const sum = d3Sum(numericData) ?? NaN;
        const count = d3Sum(bins, (bin) => bin.count);
        transformResult.data = {
            mean,
            median,
            stddev,
            min,
            max,
            bins,
            count: rawData.length,
            nulls: rawData.length - count,
            sum
        };
    }
    return transformResult;
}
/**
 * Converts an array of POJOs / features into data for histogram (bins + stats).
 * @param data The raw data
 * @param series The histogram series from the config
 * @returns The processed histogram data
 */
export function processInlineHistogramRawData(data, series) {
    let transformedData = { valid: true };
    const { rawData, dataCompatibleWithTransformation, dataCountIncompatible } = fetchInlineRawHistogramData(data, series);
    if (dataCompatibleWithTransformation === false || dataCountIncompatible === true) {
        transformedData = createInvalidDataInfoForHistogram(series.dataTransformationType, dataCountIncompatible);
    }
    else {
        transformedData = transformInlineHistogramData(series, rawData);
    }
    return transformedData;
}
