import { cloneDeep, isEmpty } from "lodash-es";
import { WebChartCurrentVersion, WebChartTypes, RESTStatisticType, WebChartDataSourceTypes, defaultPieFromFieldsName, WebChartVersionStatus } from "@arcgis/charts-spec";
import { defaultBoxPlotCategory, getDefaultChartRotation, pieChartSpecialSlicesId } from "../../defaults";
import { getSeriesType } from "../../misc";
import { ChartSubType } from "../../misc/interfaces";
import { findMatchingLayerFieldName, getFeatureLayer, getLayerFromQueryObject, getChartSubType, getSplitByField } from "../../feature-layer";
import { getChartVersionInfo, matchFieldsList } from "../utils";
import { firstWebMapVersion, unrecognizedVersion } from "../interfaces";
import { isGaugeByFeature, isPieChartFromFields } from "../../data";
import { combineFilters } from "../../query-parser";
import { isRotationSupported } from "../../type-guards";
/** **********************************************************************
 *                                                                      *
 * Routines that handle adjustments/conversions for chart version 1.0.0 *
 *                                                                      *
 *********************************************************************** */
/**
 * Modifies the chart's id if it's:
 * - empty or undefined
 * - equal to the chart's title. Used to debunk a special case related to charts from AGS Pro, where all the charts id
 * and title are the same, which generates non unique ids and causes issue later on in `ac-app`
 * @param configToUpdate The WebChart config to update
 * @returns The updated config
 */
function adjustChartId(configToUpdate) {
    const updatedConfig = cloneDeep(configToUpdate);
    if (configToUpdate !== undefined &&
        (configToUpdate.id === undefined ||
            configToUpdate.id === "" ||
            configToUpdate.id === configToUpdate.title?.content?.text))
        updatedConfig.id = `Chart_${Date.now()}`;
    return updatedConfig;
}
function adjustAxes(configToUpdate) {
    const updatedConfig = cloneDeep(configToUpdate);
    if (configToUpdate !== undefined && isEmpty(configToUpdate.axes) === false) {
        updatedConfig.axes = configToUpdate.axes?.map((axis) => {
            const newAxis = axis;
            // To fix ArcGIS Chart specification that still has these removed properties calculateAutomaticMaximum, calculateAutomaticMinimum
            delete newAxis.calculateAutomaticMaximum;
            delete newAxis.calculateAutomaticMinimum;
            if (newAxis.title?.content?.font?.style !== undefined) {
                newAxis.title.content.font.style = newAxis.title.content.font.style.toLowerCase();
            }
            return newAxis;
        });
    }
    return updatedConfig;
}
function adjustSeries(configToUpdate) {
    const updatedConfig = cloneDeep(configToUpdate);
    if (configToUpdate !== undefined && isEmpty(configToUpdate.series) === false) {
        updatedConfig.series = configToUpdate.series.map((series, seriesIndex) => {
            const newSeries = series;
            // To fix ArcGIS Pro charts that don't send the required series.id property
            if (series.id === undefined) {
                newSeries.id = `Series ${Date.now()}-${seriesIndex}`;
            }
            // ArcGIS Pro charts sends statisticType as `median` which needs to be mapped to `percentile_cont`
            const seriesType = getSeriesType(configToUpdate.series, seriesIndex);
            if ((seriesType === WebChartTypes.BarSeries || seriesType === WebChartTypes.LineSeries) &&
                newSeries.query?.outStatistics?.[0]?.statisticType === "median") {
                newSeries.query.outStatistics[0].statisticType = RESTStatisticType.ContinuousPercentile;
            }
            // convert multipleBarType to stackedType
            if (seriesType === WebChartTypes.BarSeries && newSeries.multipleBarType !== undefined) {
                newSeries.stackedType = newSeries.multipleBarType;
                delete newSeries.multipleBarType;
            }
            // convert the sliceGrouping for pie
            if (seriesType === WebChartTypes.PieSeries && newSeries.sliceGrouping !== undefined) {
                // Add a sliceId with the default value
                newSeries.sliceGrouping.sliceId = pieChartSpecialSlicesId.defaultGroupOfSlicesId;
                // rename groupName to label
                if (newSeries.sliceGrouping.groupName !== undefined) {
                    newSeries.sliceGrouping = { ...newSeries.sliceGrouping, label: newSeries.sliceGrouping.groupName };
                    delete newSeries.sliceGrouping.groupName;
                }
                // rename groupSliceSymbol to fillSymbol
                if (newSeries.sliceGrouping.groupSliceSymbol !== undefined) {
                    newSeries.sliceGrouping = {
                        ...newSeries.sliceGrouping,
                        fillSymbol: newSeries.sliceGrouping.groupSliceSymbol
                    };
                    delete newSeries.sliceGrouping.groupSliceSymbol;
                }
            }
            // convert the `x` field for box plot, this is needed for configs that come from Pro as they can have an empty `x` field
            if (seriesType === WebChartTypes.BoxPlotSeries && isEmpty(newSeries.x?.trim())) {
                newSeries.x = defaultBoxPlotCategory;
            }
            return newSeries;
        });
    }
    return updatedConfig;
}
/**
 * Matches the config properties that have field names which mismatch the layer data source fields.
 *
 * @param {*} config The chart config
 * @param {*} layer The layer instance
 * @returns {Promise<WebChart>}
 */
async function matchChartFieldsToLayerFields(configToUpdate, layer) {
    const updatedConfig = { ...configToUpdate };
    if (configToUpdate !== undefined &&
        (layer !== undefined ||
            ("dataSource" in configToUpdate && configToUpdate.dataSource?.type === WebChartDataSourceTypes.Layer)) &&
        configToUpdate.series?.length > 0) {
        const queryObject = layer ?? getFeatureLayer(configToUpdate.dataSource.layer);
        const chartSubType = getChartSubType(updatedConfig.series);
        const seriesType = getSeriesType(updatedConfig.series);
        // TODO: reevaluate condition for box plot
        const noMatchingFieldForXNeeded = (seriesType === WebChartTypes.GaugeSeries && isGaugeByFeature(configToUpdate) === false) ||
            (seriesType === WebChartTypes.PieSeries && isPieChartFromFields(configToUpdate.series) === true);
        // TODO: reevaluate condition for box plot
        const matchingFieldForYNeeded = seriesType === WebChartTypes.ScatterSeries ||
            chartSubType === ChartSubType.BarAndLineNoAggregation ||
            chartSubType === ChartSubType.PieNoAggregation;
        updatedConfig.series = await Promise.all(configToUpdate.series.map(async (series) => {
            const newSeries = { ...series };
            if (noMatchingFieldForXNeeded === false) {
                newSeries.x = await findMatchingLayerFieldName(queryObject, series.x);
            }
            if (newSeries.y !== undefined && matchingFieldForYNeeded == true) {
                const seriesY = series.y;
                // check whether y is a string or string []
                const isYArray = Array.isArray(seriesY);
                if (isYArray === true) {
                    const yArray = await matchFieldsList(seriesY, queryObject);
                    if (yArray !== undefined)
                        newSeries.y = yArray;
                }
                else {
                    newSeries.y = await findMatchingLayerFieldName(queryObject, series.y);
                }
            }
            if (series.query !== undefined) {
                let { where, groupByFieldsForStatistics, outStatistics, orderByFields } = series.query;
                groupByFieldsForStatistics = await matchFieldsList(groupByFieldsForStatistics, queryObject);
                if (orderByFields !== undefined) {
                    let fieldsList = [];
                    const sortKeys = [];
                    orderByFields.forEach((fieldSortKeyPair) => {
                        if (fieldSortKeyPair !== "" && fieldSortKeyPair !== undefined) {
                            const fieldSortKeyArr = fieldSortKeyPair.split(" ");
                            fieldsList.push(fieldSortKeyArr[0]);
                            sortKeys.push(fieldSortKeyArr[1]);
                        }
                    });
                    if (fieldsList.length > 0) {
                        fieldsList = (await matchFieldsList(fieldsList, queryObject));
                        orderByFields = fieldsList.map((item, index) => `${item} ${sortKeys[index]}`);
                    }
                }
                if (outStatistics !== undefined) {
                    outStatistics = await Promise.all(outStatistics.map(async (outField) => {
                        const newOutField = { ...outField };
                        newOutField.onStatisticField = await findMatchingLayerFieldName(queryObject, outField.onStatisticField);
                        return newOutField;
                    }));
                }
                if (where !== undefined && chartSubType === ChartSubType.BarAndLineSplitBy) {
                    const splitByField = getSplitByField(series.query?.where);
                    const whereArray = where.split("=");
                    const whereField = await findMatchingLayerFieldName(queryObject, splitByField);
                    where = `${whereField}=${whereArray[1].trim()}`;
                }
                if (newSeries.query !== undefined) {
                    if (where !== undefined)
                        newSeries.query.where = where;
                    if (groupByFieldsForStatistics !== undefined)
                        newSeries.query.groupByFieldsForStatistics = groupByFieldsForStatistics;
                    if (outStatistics !== undefined)
                        newSeries.query.outStatistics = outStatistics;
                    if (orderByFields !== undefined)
                        newSeries.query.orderByFields = orderByFields;
                }
            }
            return newSeries;
        }));
        // additional conversions for the gauge
        if (seriesType === WebChartTypes.GaugeSeries && configToUpdate?.axes?.[0] !== undefined) {
            const { minimumFromField, maximumFromField } = configToUpdate.axes[0];
            if (minimumFromField !== undefined)
                configToUpdate.axes[0].minimumFromField = await findMatchingLayerFieldName(queryObject, minimumFromField);
            if (maximumFromField !== undefined)
                configToUpdate.axes[0].maximumFromField = await findMatchingLayerFieldName(queryObject, maximumFromField);
        }
    }
    return updatedConfig;
}
/** ***********************************************************************************
 *                                                                                   *
 * Routines that handle adjustments/conversions for chart versions newer than 1.0.0  *
 *                                                                                   *
 ************************************************************************************ */
/**
 * This function renames the orderByFields field name from `arcgis_charts_pie_category_from_fields_name`
 * to `defaultPieFromFieldsName` (or `arcgis_charts_pie_from_fields_name`)
 * @param configToUpdate The chart's config to convert
 */
function adjustPieFromFieldsDefaultNameAndValue(configToUpdate) {
    const updatedConfig = cloneDeep(configToUpdate);
    if (configToUpdate !== undefined &&
        isEmpty(configToUpdate.series) === false &&
        isPieChartFromFields(configToUpdate.series) === true) {
        // convert the default name/value for PieFromFields
        // should only have one series
        updatedConfig.series = configToUpdate.series.map((series) => {
            const newSeries = cloneDeep(series);
            if (series.query?.orderByFields?.[0] !== undefined &&
                series.query?.orderByFields?.[0].includes("arcgis_charts_pie_category_from_fields_name") === true) {
                newSeries.query.orderByFields[0] = series.query.orderByFields[0].replace("arcgis_charts_pie_category_from_fields_name", defaultPieFromFieldsName);
            }
            return newSeries;
        });
    }
    return updatedConfig;
}
/**
 * Remove the optional `spacing` property for gauge chart (from version 2.0.0).
 * @param configToUpdate
 * @returns
 */
function removeSpacingPropertyFromGauge(configToUpdate) {
    const updatedConfig = cloneDeep(configToUpdate);
    if (configToUpdate !== undefined && isEmpty(configToUpdate.series) === false) {
        const seriesType = getSeriesType(configToUpdate.series);
        if (seriesType === WebChartTypes.GaugeSeries && configToUpdate.axes?.[0] !== undefined) {
            const newAxis = configToUpdate.axes[0];
            if (newAxis.ticks?.hasOwnProperty("spacing") === true)
                delete newAxis.ticks.spacing;
            updatedConfig.axes[0] = newAxis;
        }
    }
    return updatedConfig;
}
/**
 * Moves the series[x].query.extent to config.dataFilters.geometry, and delete
 * the extent properties from all the series query.
 * @param configToUpdate The chart's config to update
 */
function moveSeriesQueryExtent(configToUpdate) {
    const updatedConfig = cloneDeep(configToUpdate);
    // getting the extent from the first series query
    const { extent } = updatedConfig.series[0]?.query ?? {};
    if (extent !== undefined) {
        // copying it to the config data filters
        updatedConfig.dataFilters = { ...updatedConfig.dataFilters, geometry: extent };
        // deleting the `extent` from all the series query
        updatedConfig.series.forEach((series) => {
            // deleting the former property `extent` from the query
            delete series.query?.extent;
        });
    }
    return updatedConfig;
}
/**
 * For charts that offer rotation feature, this function will
 * - Move the optional `rotated` property  from the series to the chart object
 * if the chart object does not contain this property.
 * - Remove all series' rotated property.
 * @param configToUpdate config to update
 * @returns the updated config
 */
function moveSeriesRotatedPropToChart(configToUpdate) {
    const updatedConfig = cloneDeep(configToUpdate);
    if (configToUpdate !== undefined && isEmpty(configToUpdate.series) === false) {
        // If the type of chart supports the rotation, we move the series' rotated property to the chart object
        if (isRotationSupported(configToUpdate) && configToUpdate.rotated === undefined) {
            // if the chart rotated property is not set, set it using the first series' rotated property or use the default value
            updatedConfig.rotated = configToUpdate.series[0].rotated ?? getDefaultChartRotation();
        }
        // remove the series' rotated property if present
        updatedConfig.series.forEach((series) => {
            if (series.rotated !== undefined) {
                delete series.rotated;
            }
        });
    }
    return updatedConfig;
}
/**
 * In version 5.0.0 we modified property names in the config dataSource
 * @param configToUpdate config to update
 * @returns the updated config
 */
function renameDataSourceProperties(configToUpdate) {
    const updatedConfig = cloneDeep(configToUpdate);
    const oldDataSource = configToUpdate.dataSource;
    if (configToUpdate !== undefined && "dataSource" in updatedConfig && isEmpty(oldDataSource) === false) {
        if (oldDataSource.type === "featureLayer")
            updatedConfig.dataSource.type = "layer";
        if ("featureLayer" in oldDataSource) {
            updatedConfig.dataSource.layer = oldDataSource.featureLayer;
            delete updatedConfig.dataSource.featureLayer;
        }
    }
    return updatedConfig;
}
/**
 * In version 6.0.0 we removed the property config.dataSource.filter, replaced by config.dataFilters.where
 * @param configToUpdate config to update
 * @returns the updated config
 */
function copyDataSourceFilterToConfig(configToUpdate) {
    const updatedConfig = cloneDeep(configToUpdate);
    if ("dataSource" in configToUpdate) {
        // typing the dataSource to convert to make clearer typings below
        const oldDataSource = configToUpdate.dataSource;
        if (oldDataSource?.filter !== undefined && oldDataSource.filter?.trim() !== "") {
            const combinedFilter = combineFilters([oldDataSource.filter, updatedConfig.dataFilters?.where]);
            // combining the existing dataFilters.where with the dataSource.filter
            updatedConfig.dataFilters = { ...updatedConfig.dataFilters, where: combinedFilter };
            // deleting the filter property
            delete updatedConfig.dataSource.filter;
        }
    }
    return updatedConfig;
}
/**
 * This function will
 * - Copy the optional `showOutliers` property from the series to the chart object (if not already defined)
 * - Copy the optional `standardizeValues` property from the series to the chart object (if not already defined)
 * - Delete the series `showOutliers`
 * - Delete the series `standardizeValues`
 *
 * @param configToUpdate config to update
 * @returns the updated config
 */
function moveBoxPlotSeriesPropsToChart(configToUpdate) {
    const updatedConfig = cloneDeep(configToUpdate);
    if (configToUpdate !== undefined && isEmpty(configToUpdate.series) === false) {
        const seriesType = getSeriesType(configToUpdate.series);
        if (seriesType === WebChartTypes.BoxPlotSeries) {
            const firstSeries = updatedConfig.series[0];
            if (firstSeries.showOutliers !== undefined) {
                if (configToUpdate.showOutliers === undefined) {
                    // copying the property to the chart level
                    updatedConfig.showOutliers = firstSeries.showOutliers;
                }
                // deleting the series property
                delete firstSeries.showOutliers;
            }
            if (firstSeries.standardizeValues !== undefined) {
                if (configToUpdate.standardizeValues === undefined) {
                    // copying the property to the chart level
                    updatedConfig.standardizeValues = firstSeries.standardizeValues;
                }
                // deleting the series property
                delete firstSeries.standardizeValues;
            }
        }
    }
    return updatedConfig;
}
/**
 * For Pie chart, this function will
 * - Copy the optional `sortLabelsBy` property from the pie series to the chart object (if not already defined)
 * - Delete the pie series `sortLabelsBy`
 * @param configToUpdate config to update
 * @returns the updated config
 */
function movePieSeriesSortLabelsPropToChart(configToUpdate) {
    const updatedConfig = cloneDeep(configToUpdate);
    if (configToUpdate !== undefined && isEmpty(configToUpdate.series) === false) {
        const seriesType = getSeriesType(configToUpdate.series);
        if (seriesType === WebChartTypes.PieSeries) {
            const pieSeries = updatedConfig.series[0];
            if (pieSeries.sortLabelsBy !== undefined) {
                if (configToUpdate.sortLabelsBy === undefined) {
                    // copying the property to the chart level
                    updatedConfig.sortLabelsBy = pieSeries.sortLabelsBy;
                }
                // deleting the series property
                delete pieSeries.sortLabelsBy;
            }
        }
    }
    return updatedConfig;
}
/** **********************************************************************************************************
 *                                                                                                          *
 * Conversion functions to be called in `executeConversionFunctions()`                                      *
 * For ease of maintenance, it's recommended to name the functions as `convertTo_<major>_<minor>_<patch>()` *
 *                                                                                                          *
 *********************************************************************************************************** */
async function convertTo_1_0_0(configToUpdate, layer) {
    let updatedConfig = cloneDeep(configToUpdate);
    if (updatedConfig !== undefined) {
        // chart id
        updatedConfig = adjustChartId(updatedConfig);
        // Axes adjustment
        updatedConfig = adjustAxes(updatedConfig);
        // Series adjustment
        updatedConfig = adjustSeries(updatedConfig);
        // handle field casing issue
        updatedConfig = await matchChartFieldsToLayerFields(updatedConfig, layer);
        // Move box plot specific properties at the chart level
        updatedConfig = moveBoxPlotSeriesPropsToChart(updatedConfig);
        // handle number types
        if (updatedConfig.legend !== undefined && Number.isFinite(updatedConfig.legend.maxHeight) === false) {
            updatedConfig.legend.maxHeight = NaN;
        }
    }
    return updatedConfig;
}
/**
 * Conversions for 1.0.1:
 *   - orderByField adjustment for PieFromFields
 * @param configToUpdate The chart's config to convert
 * @returns The converted config
 */
function convertTo_1_0_1(configToUpdate) {
    let updatedConfig = cloneDeep(configToUpdate);
    if (configToUpdate !== undefined) {
        // orderByField adjustment for PieFromFields
        updatedConfig = adjustPieFromFieldsDefaultNameAndValue(updatedConfig);
    }
    return updatedConfig;
}
/**
 * Conversions for 2.0.0:
 *   - Removing the 'spacing' property from the gauge config
 * @param configToUpdate The chart's config to convert
 * @returns The converted config
 */
function convertTo_2_0_0(configToUpdate) {
    let updatedConfig = cloneDeep(configToUpdate);
    if (configToUpdate !== undefined) {
        updatedConfig = removeSpacingPropertyFromGauge(updatedConfig);
    }
    return updatedConfig;
}
/**
 * Conversions for 3.5.0:
 *   - Moving the extent from the first series query to the config data filters geometry
 * @param configToUpdate The chart's config to convert
 * @returns The converted config
 */
function convertTo_3_5_0(configToUpdate) {
    let updatedConfig = cloneDeep(configToUpdate);
    if (configToUpdate !== undefined) {
        // renaming extent
        updatedConfig = moveSeriesQueryExtent(updatedConfig);
    }
    return updatedConfig;
}
/**
 * Conversions for 4.0.1:
 *   - Moving the rotated property from the first series to the chart object.
 * @param configToUpdate The chart's config to convert
 * @returns The converted config
 */
function convertTo_4_0_1(configToUpdate) {
    let updatedConfig = cloneDeep(configToUpdate);
    if (configToUpdate !== undefined) {
        // handle rotated property
        updatedConfig = moveSeriesRotatedPropToChart(updatedConfig);
    }
    return updatedConfig;
}
/**
 * Conversions for 5.0.0:
 *   - Renaming the datasource properties.
 * @param configToUpdate The chart's config to convert
 * @returns The converted config
 */
function convertTo_5_0_0(configToUpdate) {
    let updatedConfig = cloneDeep(configToUpdate);
    if (configToUpdate !== undefined) {
        // handle rotated property
        updatedConfig = renameDataSourceProperties(updatedConfig);
    }
    return updatedConfig;
}
/**
 * Conversions for 7.0.0:
 *   - Copying the removed config.dataSource.filter to config.dataFilters.where
 * @param configToUpdate The chart's config to convert
 * @returns The converted config
 */
function convertTo_7_0_0(configToUpdate) {
    let updatedConfig = cloneDeep(configToUpdate);
    if (configToUpdate !== undefined) {
        // handle rotated property
        updatedConfig = copyDataSourceFilterToConfig(updatedConfig);
    }
    return updatedConfig;
}
/**
 * Conversions for 7.4.1:
 *   - Move the `SortLabelsBy` property from the Pie series to the chart object.
 * @param configToUpdate The chart's config to convert
 * @returns The converted config
 */
function convertTo_7_4_1(configToUpdate) {
    let updatedConfig = cloneDeep(configToUpdate);
    if (configToUpdate !== undefined) {
        updatedConfig = movePieSeriesSortLabelsPropToChart(updatedConfig);
    }
    return updatedConfig;
}
/**
 * Apply the needed function calls depending on the current chart version.
 * Note: Additional switch cases and convertToxxx functions are to be added to this function accordingly.

 * For example: if a newer spec version "X.Y.Z" has been released, considering the former version is "E.F.G" and the one before "A.B.C"
 * 1. Case 1: the new spec version "X.Y.Z" requires a conversion:
 *   1a. Create a new function called `convertTo_X_Y_Z()`
 *   1b. Have additional subroutines if needed to be called inside the `convertTo_X_Y_Z()`
 *   1c. Add a new case to the switch for the former version "E.F.G" that calls the new `convertTo_X_Y_Z()`
 *   1d. Add a call to `convertTo_X_Y_Z()` in the block testing if the versions to convert includes the first web map version or an unrecognized one
 * 2. Case 2: a conversion is not required
 *   2a. Rename the function `convertTo_E_F_G()` into `convertTo_X_Y_Z()`
 *   2b. Change the switch case "A.B.C" calling `convertTo_E_F_G()` into a switch case "E.F.G" calling `convertTo_X_Y_Z()`
 *
 * @param props.versionsToConvert a list of chart versions to be converted.
 * @param props.configToUpdate the input chart config
 * @param props.layer the layer instance
 * @returns the converted chart config
 */
async function applyConversions(props) {
    const { versionsToConvert = [], configToUpdate, layer } = props;
    let updatedConfig = cloneDeep(configToUpdate);
    if (versionsToConvert.length > 0 && configToUpdate !== undefined) {
        // `convertTo_1_0_0()` contains mandatory conversions unrelated to a spec version, hence we call this function no matter what
        updatedConfig = await convertTo_1_0_0(updatedConfig, layer);
        // we convert everything if the array contains the first spec version or an unrecognized one
        if (versionsToConvert.includes(firstWebMapVersion) || versionsToConvert.includes(unrecognizedVersion)) {
            updatedConfig = convertTo_1_0_1(updatedConfig);
            updatedConfig = convertTo_2_0_0(updatedConfig);
            updatedConfig = convertTo_3_5_0(updatedConfig);
            updatedConfig = convertTo_4_0_1(updatedConfig);
            updatedConfig = convertTo_5_0_0(updatedConfig);
            updatedConfig = convertTo_7_0_0(updatedConfig);
            updatedConfig = convertTo_7_4_1(updatedConfig);
        }
        else {
            // else we go through each version item and apply the appropriate conversions
            versionsToConvert.forEach((versionItem) => {
                switch (versionItem) {
                    case "1.0.1":
                        updatedConfig = convertTo_2_0_0(updatedConfig);
                        break;
                    case "3.4.0":
                        updatedConfig = convertTo_3_5_0(updatedConfig);
                        break;
                    case "4.0.0":
                        updatedConfig = convertTo_4_0_1(updatedConfig);
                        break;
                    case "4.0.1":
                        updatedConfig = convertTo_5_0_0(updatedConfig);
                        break;
                    case "6.4.0":
                        updatedConfig = convertTo_7_0_0(updatedConfig);
                        break;
                    case "7.4.0":
                        updatedConfig = convertTo_7_4_1(updatedConfig);
                        break;
                    case WebChartCurrentVersion: // do nothing
                    default:
                        break;
                }
            });
        }
        // update config to the current version number
        updatedConfig.version = WebChartCurrentVersion;
    }
    return updatedConfig;
}
/**
 * Main function that converts the chart config with older chart JSON Schema, comparing to the current version.
 * @param {*} config The chart config
 * @param {*} layer The layer instance
 * @returns the updated web chart config if it is converted; otherwise the same input chart config.
 */
export async function convertChartSpec(config, layerOrLayerView) {
    // get the version of the chart config; if the version is null set it to the first version since we will try to convert it anyway
    const configVersion = config?.version ?? firstWebMapVersion;
    let newConfig = config;
    // get chart version info
    const { versionStatus, versions } = getChartVersionInfo(configVersion);
    // if the version is newer, should treat it at least at the current version and we won't need to do any conversion.
    // if it's the current version, no need to convert either
    if (newConfig !== undefined &&
        (versionStatus === WebChartVersionStatus.Older || versionStatus === WebChartVersionStatus.Unrecognized)) {
        const layer = getLayerFromQueryObject(layerOrLayerView);
        newConfig = await applyConversions({ versionsToConvert: versions, configToUpdate: newConfig, layer });
    }
    return newConfig;
}
