import { RESTStatisticType, WebChartTypes, WebChartVersionStatus } from "@arcgis/charts-spec";
import { WebChartJSONSchemas } from "@arcgis/charts-spec/dist/json-schema";
import Ajv from "ajv";
import { fetchLayerFieldsInfo, isNumericField, getLayerFromQueryObject, getFieldFromName, getChartSubType, isFeatureLayerView } from "../../feature-layer";
import { ConfigErrorKeywords, ConfigWarningKeywords, DataWarningKeywords } from "./interfaces";
import { formatMessage, getLocaleInfo } from "../../localization";
import { fetchErrorsStrings } from "../../t9n/errors";
import { getChartLimits } from "../../defaults";
import { ErrorKeywords } from "../../errors/interfaces";
import { getSeriesType } from "../../misc";
import { ChartSubType } from "../../misc/interfaces";
import { getChartVersionInfo } from "../utils";
import { fetchCommonStrings } from "../../t9n/common";
/**
 * Extracts schema name from the schema `$ref` path
 * For e.g.:
 *   - "#/definitions/WebChartInlineDataSource" => "WebChartInlineDataSource"
 *   - "#/definitions/WebChartBarChartSeries" => "WebChartBarChartSeries"
 * @param schemaRefPath
 */
function extractSchemaNameFromRefPath(schemaRefPath) {
    const schemaStartIndex = schemaRefPath.lastIndexOf("/");
    return schemaStartIndex !== -1 ? schemaRefPath.slice(schemaStartIndex + 1) : schemaRefPath;
}
async function customizeValidationErrors(errors) {
    const errorStrings = await fetchErrorsStrings();
    return errors.map((error) => {
        let customizedError = error;
        const dataPath = error.dataPath[0] === "." ? error.dataPath.substring(1) : error.dataPath;
        switch (error.keyword) {
            case ConfigErrorKeywords.Required:
                customizedError = {
                    ...error,
                    message: formatMessage(errorStrings.requiredProperty, {
                        dataPath,
                        missingProperty: error.params.missingProperty
                    })
                };
                break;
            case ConfigErrorKeywords.MinLength:
                // TODO: Figure out if it uses LimitParams or Comparison Params
                customizedError = {
                    ...error,
                    message: formatMessage(errorStrings.minLength, {
                        dataPath,
                        limit: error.params.limit.toString()
                    })
                };
                break;
            case ConfigErrorKeywords.MinItems:
                customizedError = {
                    ...error,
                    message: formatMessage(errorStrings.minItems, {
                        dataPath,
                        limit: error.params.limit.toString()
                    })
                };
                break;
            case ConfigErrorKeywords.MaxItems:
                customizedError = {
                    ...error,
                    message: formatMessage(errorStrings.maxItems, {
                        dataPath,
                        limit: error.params.limit.toString()
                    })
                };
                break;
            case ConfigErrorKeywords.Pattern:
                if (error.params.pattern === "\\w+") {
                    customizedError = {
                        ...error,
                        message: formatMessage(errorStrings.whiteSpacePattern, { dataPath }),
                        params: {}
                    };
                }
                break;
            case ConfigErrorKeywords.AdditionalProperties:
                customizedError = {
                    ...error,
                    message: formatMessage(errorStrings.additionalProperty, {
                        dataPath,
                        additionalProperty: error.params.additionalProperty
                    })
                };
                break;
            case ConfigErrorKeywords.Enum:
                customizedError = {
                    ...error,
                    message: formatMessage(errorStrings.enumValues, {
                        dataPath,
                        allowedValues: error.params.allowedValues.join(", ")
                    })
                };
                break;
            case ConfigErrorKeywords.AnyOf: {
                let params = [];
                if (error.schemaPath === "#/properties/legend/anyOf") {
                    params = WebChartJSONSchemas.definitions.WebChart.properties.legend.anyOf.map((schemaRef) => {
                        return extractSchemaNameFromRefPath(schemaRef.$ref);
                    });
                }
                customizedError = {
                    ...error,
                    params,
                    message: formatMessage(errorStrings.anyOfValues, {
                        dataPath,
                        schemaOptions: params.join(", ")
                    })
                };
                break;
            }
            default:
                break;
        }
        return customizedError;
    });
}
/**
 * By default, the JSON schema created by ac-spec is suitable for WebChart validation.
 * If we want to validate any other object, an appropriate schema can be generated via this function.
 * @param schema
 * @param schemaKey
 */
function createStartingSchema(schema, schemaKey) {
    return { ...schema, $ref: `#/definitions/${schemaKey}` };
}
async function findLayerLoadError(queryObject) {
    let error;
    const errorStrings = await fetchErrorsStrings();
    const layer = getLayerFromQueryObject(queryObject);
    try {
        await layer.load();
    }
    catch (e) {
        const url = layer?.url ?? "";
        const portalItemId = layer?.portalItem?.id ?? "";
        error = {
            keyword: ErrorKeywords.LayerLoadFailure,
            message: formatMessage(errorStrings.layerLoadFailure, { url, portalItemId }),
            params: { url, portalItemId }
        };
    }
    return error;
}
/**
 * Generates AJV Validator function.
 * Returns a closure having a reference to the validator function.
 * Refer to [Getting Started with Ajv](https://ajv.js.org/#getting-started) for more details.
 * @param allErrors
 */
function generateValidatorFunction() {
    const validatorInstance = new Ajv({ allErrors: true });
    return async (input, seriesType) => {
        let chartJSONSchema = WebChartJSONSchemas; // By default, the schema is ready for WebChart validation.
        // TODO: Replace with switch cases at some point...
        if (seriesType === WebChartTypes.GaugeSeries) {
            chartJSONSchema = createStartingSchema(WebChartJSONSchemas, "WebGaugeChart"); // Modifying the schema for WebGaugeChart validation.
        }
        if (input?.dataSource === undefined) {
            chartJSONSchema = createStartingSchema(WebChartJSONSchemas, "WebMapWebChart"); // Modifying the schema for WebMapWebChart validation.
        }
        const validatorFunction = validatorInstance.compile(chartJSONSchema);
        validatorFunction(input); // asserting as boolean because our validation is not async
        let errors = [];
        if (validatorFunction.errors !== null && validatorFunction.errors !== undefined) {
            errors = await customizeValidationErrors(validatorFunction.errors);
        }
        return errors;
    };
}
/**
 * Finds all the errors in the `WebChart` config at once. If the first error is on say, line 10, then the error
 * reported on line 20 may not be accurate until the error on line 10 is fixed first.
 */
const findSchemaErrors = generateValidatorFunction();
/**
 * Check whether there exists a `StatisticDefinition` in any series' `Query` where non-count aggregation is applied to a non-numeric field
 * @param seriesArray
 * @param queryObject
 */
async function findNumericAggregationWarnings(seriesArray, queryObject) {
    const layerFields = await fetchLayerFieldsInfo(queryObject);
    const warnings = [];
    const errorStrings = await fetchErrorsStrings();
    for (let i = 0; i < seriesArray.length; i += 1) {
        const outStatistics = seriesArray[i].query?.outStatistics;
        if (outStatistics !== undefined) {
            for (let j = 0; j < outStatistics.length; j += 1) {
                const statDefinition = outStatistics[j];
                const field = getFieldFromName(statDefinition.onStatisticField, layerFields);
                if (field !== undefined &&
                    isNumericField(field) === false &&
                    statDefinition.statisticType !== RESTStatisticType.Count) {
                    const dataPath = `series[${i}].query.outStatistics[${j}]`;
                    warnings.push({
                        keyword: ConfigWarningKeywords.NonNumericAggregation,
                        message: formatMessage(errorStrings.nonNumericAggregation, { dataPath }),
                        dataPath,
                        params: {
                            fieldName: statDefinition.onStatisticField,
                            fieldType: field.type,
                            aggregationRequested: statDefinition.statisticType
                        }
                    });
                }
            }
        }
    }
    return warnings;
}
/**
 * Verifies that the chart series are unique. Sets a message to display if not.
 * @param chartConfig
 */
async function findDuplicateSeriesIDWarnings(chartConfig) {
    const warnings = [];
    const errorStrings = await fetchErrorsStrings();
    if (chartConfig !== undefined && chartConfig.series.length > 1) {
        // Creating a Set which will contain the list of the series id without duplicates
        const listIds = new Set();
        let previousCount = 0;
        chartConfig.series.forEach((series, seriesIndex) => {
            listIds.add(series.id);
            // if no new id has been added to the set, it means its a duplicate
            if (listIds.size === previousCount) {
                const dataPath = `series[${seriesIndex}].id`;
                warnings.push({
                    keyword: ConfigWarningKeywords.DuplicateSeriesID,
                    message: formatMessage(errorStrings.duplicateSeriesID, {
                        dataPath,
                        seriesID: series.id,
                        seriesName: series.name
                    }),
                    dataPath,
                    params: {
                        seriesId: series.id,
                        seriesName: series.name
                    }
                });
            }
            else {
                previousCount += 1;
            }
        });
    }
    return warnings;
}
async function findWarnings(input, queryObject) {
    let warnings = [];
    if (queryObject !== undefined) {
        const numericAggregationWarnings = await findNumericAggregationWarnings(input.series, queryObject);
        warnings = warnings.concat(numericAggregationWarnings);
    }
    warnings = warnings.concat(await findDuplicateSeriesIDWarnings(input));
    return warnings;
}
async function findCustomErrors(queryObject) {
    let errors = [];
    // Error: the layer can't load
    if (queryObject !== undefined) {
        const layerLoadError = await findLayerLoadError(queryObject);
        if (layerLoadError !== undefined)
            errors = errors.concat(layerLoadError);
    }
    return errors;
}
/**
 * Validates the `WebChart` config by checking for  and displays `Errors` and `Warnings`.
 * - `Errors` are violations in the config w.r.t the JSON schema.
 * - `Warnings` are undesired combinations in the config which don't violate the JSON schema but should still be blocked.
 * @param props
 */
export async function validateWebChart(props) {
    const { input, seriesType, queryObject } = props;
    // TODO: CT: add a function for unsupported chart type and call it here?
    let status;
    if (getChartVersionInfo(input?.version).versionStatus === WebChartVersionStatus.Newer) {
        // skip validating the web chart if it's newer
        status = { valid: true, schemaErrors: [], customErrors: [], warnings: [] };
    }
    else {
        const schemaErrors = await findSchemaErrors(input, seriesType);
        let warnings = [];
        let customErrors = [];
        if (input !== undefined && schemaErrors.length === 0) {
            customErrors = await findCustomErrors(queryObject);
            if (customErrors.length === 0) {
                warnings = await findWarnings(input, queryObject);
            }
        }
        status = {
            valid: schemaErrors.length === 0 && warnings.length === 0 && customErrors.length === 0,
            schemaErrors,
            customErrors,
            warnings
        };
    }
    return status;
}
/**
 * Creates a warning object
 * @param props
 */
export async function getDataWarningObject(props) {
    const { invalidType, chartLimits, elementCount, seriesCount, queryObject, filterBySelection, locale = getLocaleInfo().locale, formatLocale = getLocaleInfo().formatLocale } = props;
    const errorStrings = await fetchErrorsStrings(locale);
    const commonStrings = await fetchCommonStrings(locale);
    let message = errorStrings.defaultInvalidChart;
    let params;
    const basicNumberFormatter = new Intl.NumberFormat(formatLocale);
    const limits = getChartLimits(chartLimits);
    switch (invalidType) {
        case DataWarningKeywords.BarElementExceedsLimit: {
            let totalLimit = NaN;
            let seriesLimit = NaN;
            let errorMessage = "";
            // adjusting the message and its parameters according to the number of series
            if (seriesCount !== undefined) {
                if (seriesCount <= limits.maxBarChartSeriesCount) {
                    if (seriesCount === 1) {
                        errorMessage = errorStrings.uniqueSeriesBarCountCannotExceedLimit;
                        totalLimit = limits.maxBarUniqueSeriesCountTotal;
                    }
                    else if (seriesCount === 2) {
                        errorMessage = errorStrings.twoSeriesBarCountCannotExceedLimit;
                        totalLimit = limits.maxBarTwoSeriesCountTotal;
                        seriesLimit = limits.maxBarTwoSeriesCountPerSeries;
                    }
                    else if (seriesCount > 2) {
                        errorMessage = errorStrings.threePlusSeriesBarCountCannotExceedLimit;
                        totalLimit = limits.maxBarThreePlusSeriesCountTotal;
                        seriesLimit = limits.maxBarThreePlusSeriesCountPerSeries;
                    }
                }
                else {
                    errorMessage = errorStrings.barSeriesCountCannotExceedLimit;
                    seriesLimit = limits.maxBarChartSeriesCount;
                }
            }
            const formattedTotalLimit = basicNumberFormatter.format(totalLimit);
            const formattedSeriesLimit = basicNumberFormatter.format(seriesLimit);
            const formattedElementCount = elementCount !== undefined ? basicNumberFormatter.format(elementCount) : "";
            message = formatMessage(errorMessage, {
                totalLimit: formattedTotalLimit,
                seriesLimit: formattedSeriesLimit,
                elementCount: formattedElementCount
            });
            params = { actualCount: elementCount ?? NaN, totalLimit, seriesLimit };
            break;
        }
        case DataWarningKeywords.LineChartExceedsLimit:
            {
                let totalLimit = NaN;
                let seriesLimit = NaN;
                let errorMessage = "";
                // adjusting the message and its parameters according to the number of series
                if (seriesCount !== undefined) {
                    if (seriesCount <= limits.maxLineChartSeriesCount) {
                        if (seriesCount === 1) {
                            errorMessage = errorStrings.uniqueSeriesLineCountCannotExceedLimit;
                            totalLimit = limits.maxLineUniqueSeriesCountTotal;
                        }
                        else if (seriesCount === 2) {
                            errorMessage = errorStrings.twoSeriesLineCountCannotExceedLimit;
                            totalLimit = limits.maxLineTwoSeriesCountTotal;
                            seriesLimit = limits.maxLineTwoSeriesCountPerSeries;
                        }
                        else if (seriesCount > 2) {
                            errorMessage = errorStrings.threePlusSeriesLineCountCannotExceedLimit;
                            totalLimit = limits.maxLineThreePlusSeriesCountTotal;
                            seriesLimit = limits.maxLineThreePlusSeriesCountPerSeries;
                        }
                    }
                    else {
                        errorMessage = errorStrings.lineSeriesCountCannotExceedLimit;
                        seriesLimit = limits.maxLineChartSeriesCount;
                    }
                }
                const formattedTotalLimit = basicNumberFormatter.format(totalLimit);
                const formattedSeriesLimit = basicNumberFormatter.format(seriesLimit);
                const formattedElementCount = elementCount !== undefined ? basicNumberFormatter.format(elementCount) : "";
                message = formatMessage(errorMessage, {
                    elementCount: formattedElementCount,
                    seriesLimit: formattedSeriesLimit,
                    totalLimit: formattedTotalLimit
                });
                params = { actualCount: elementCount ?? NaN, totalLimit, seriesLimit };
            }
            break;
        case DataWarningKeywords.BoxPlotExceedsLimit:
            {
                let totalLimit = NaN;
                let seriesLimit = NaN;
                let errorMessage = "";
                if (seriesCount !== undefined) {
                    if (seriesCount <= limits.maxBoxPlotSeriesCount) {
                        if (seriesCount === 1) {
                            errorMessage = errorStrings.uniqueSeriesBoxCountCannotExceedLimit;
                            totalLimit = limits.maxBoxUniqueSeriesCountTotal;
                        }
                        else if (seriesCount === 2) {
                            errorMessage = errorStrings.twoSeriesBoxCountCannotExceedLimit;
                            totalLimit = limits.maxBoxTwoSeriesCountTotal;
                            seriesLimit = limits.maxBoxTwoSeriesCountPerSeries;
                        }
                        else if (seriesCount > 2) {
                            errorMessage = errorStrings.threePlusBoxLineCountCannotExceedLimit;
                            totalLimit = limits.maxBoxThreePlusSeriesCountTotal;
                            seriesLimit = limits.maxBoxThreePlusSeriesCountPerSeries;
                        }
                    }
                    else {
                        errorMessage = errorStrings.boxSeriesCountCannotExceedLimit;
                        seriesLimit = limits.maxBoxPlotSeriesCount;
                    }
                }
                const formattedTotalLimit = basicNumberFormatter.format(totalLimit);
                const formattedSeriesLimit = basicNumberFormatter.format(seriesLimit);
                const formattedElementCount = elementCount !== undefined ? basicNumberFormatter.format(elementCount) : "";
                message = formatMessage(errorMessage, {
                    elementCount: formattedElementCount,
                    seriesLimit: formattedSeriesLimit,
                    totalLimit: formattedTotalLimit
                });
                params = { actualCount: elementCount ?? NaN, totalLimit, seriesLimit };
            }
            break;
        case DataWarningKeywords.BoxPlotOutliersExceedsLimit:
            {
                const errorMessage = errorStrings.boxSeriesOutlierCannotExceedLimit;
                const totalLimit = limits.maxBoxPlotOutlierLimit;
                const formattedTotalLimit = basicNumberFormatter.format(totalLimit);
                message = formatMessage(errorMessage, {
                    totalLimit: formattedTotalLimit
                });
                params = { actualCount: elementCount ?? NaN, totalLimit };
            }
            break;
        case DataWarningKeywords.PieChartExceedsLimit:
            {
                const errorMessage = errorStrings.pieChartSlicesCannotExceedLimit;
                const sliceLimit = limits.maxPieChartSliceCountTotal;
                const formattedSliceLimit = basicNumberFormatter.format(sliceLimit);
                const sliceCount = elementCount !== undefined ? basicNumberFormatter.format(elementCount) : "";
                message = formatMessage(errorMessage, {
                    sliceCount,
                    totalLimit: formattedSliceLimit
                });
                params = { actualCount: elementCount ?? NaN, totalLimit: sliceLimit };
            }
            break;
        case DataWarningKeywords.GaugeExceedsLimit:
            {
                const errorMessage = errorStrings.gaugeCannotExceedLimit;
                const featureLimit = limits.maxGaugeByFeaturesLimit;
                const formattedFeatureLimit = basicNumberFormatter.format(featureLimit);
                const featureCount = elementCount !== undefined ? basicNumberFormatter.format(elementCount) : "";
                message = formatMessage(errorMessage, {
                    featureCount,
                    totalLimit: formattedFeatureLimit
                });
                params = { actualCount: elementCount ?? NaN, totalLimit: featureLimit };
            }
            break;
        case DataWarningKeywords.PieChartMixOfNegativePositiveSlices:
            message = errorStrings.pieChartCannotHaveMixtureOfPositiveAndNegativeSlices;
            break;
        case DataWarningKeywords.NegativeValueInSqrtCalculation:
            message = errorStrings.negativeValueInDataForSqrtTransformation;
            break;
        case DataWarningKeywords.NegativeValueInLogCalculation:
            message = errorStrings.negativeValueInDataForLogTransformation;
            break;
        case DataWarningKeywords.HistogramEmptyField:
            message = errorStrings.histogramEmptyField;
            break;
        case DataWarningKeywords.EmptyDataSet:
            // case when FBS is enabled
            if (filterBySelection === true) {
                message = commonStrings.noDataFBSMessage;
            }
            // case when FBE is enabled
            else if (isFeatureLayerView(queryObject) === true) {
                message = commonStrings.noDataFBEMessage;
            }
            else {
                message = commonStrings.noDataMessage;
            }
            break;
        case DataWarningKeywords.BadData:
        default:
            break;
    }
    const warningObject = { keyword: invalidType ?? DataWarningKeywords.BadData, message, params };
    return warningObject;
}
/**
 * Checks whether the provided config series match the expected type(s) for the component.
 * Displays an error message in the console if a series type doesn't match the expected one(s).
 * @param expectedType A list of expected series types
 * @param config The chart config containing the series
 */
export async function validateSeriesType(expectedType, config) {
    if (config?.series !== undefined) {
        const errorStrings = await fetchErrorsStrings();
        // checking each series
        for (let iSeries = 0; iSeries < config.series.length; iSeries += 1) {
            const currentSeries = config.series[iSeries];
            // Getting the series type. Not using `getSeriesType` as it doesn't handle unknown types
            const seriesType = currentSeries.type;
            if (expectedType.includes(seriesType) === false) {
                const formattedErrorMessage = formatMessage(errorStrings.invalidSeriesType, {
                    receivedType: seriesType,
                    seriesIndex: `${iSeries}`,
                    expectedType: expectedType.join(` ${errorStrings.or} `) // e.g. "barSeries or lineSeries"
                });
                console.error(formattedErrorMessage);
            }
        }
    }
}
/**
 * Checks whether the config is "partial", meaning it has been initialized for a given chart type
 * but has not been provided the minimum required prop(s) yet.
 * @param config The config to analyze
 */
export function isPartialConfig(config) {
    const chartType = getSeriesType(config?.series);
    let isPartial = false;
    switch (chartType) {
        case WebChartTypes.BarSeries:
        case WebChartTypes.LineSeries:
        case WebChartTypes.HistogramSeries: {
            // Bar Chart, Line Chart, or Histogram missing "x" field is a partial config
            isPartial = (config?.series?.[0]?.x?.length ?? 0) === 0;
            break;
        }
        case WebChartTypes.ScatterSeries: {
            // Scatter Plot missing "x" field or "y" field is a partial config
            const scatterSeries = config?.series;
            isPartial = (scatterSeries?.[0]?.x?.length ?? 0) === 0 || (scatterSeries?.[0]?.y?.length ?? 0) === 0;
            break;
        }
        case WebChartTypes.PieSeries: {
            const pieSeries = config?.series;
            // Check if pie is in "Fields" mode or "Category" mode
            if (getChartSubType(pieSeries) === ChartSubType.PieFromFields) {
                // In "Fields" mode, Pie Chart with no numeric fields is a partial config
                isPartial = (pieSeries?.[0]?.query?.outStatistics?.length ?? 0) === 0;
            }
            else {
                // In "Category" mode, Pie Chart missing "x" field is a partial config
                isPartial = (pieSeries?.[0]?.x?.length ?? 0) === 0;
            }
            break;
        }
        case WebChartTypes.BoxPlotSeries: {
            const boxPlotSeries = config?.series;
            // Box Plot missing "y" field is a partial config
            isPartial = [boxPlotSeries?.[0]?.y].flat().length === 0 || boxPlotSeries?.[0]?.y === "";
            break;
        }
        default:
            break;
    }
    return isPartial;
}
