import { RESTSymbolType, WebChartDataSourceTypes, WebChartTypes } from "@arcgis/charts-spec";
import { getSeriesType } from "../misc";
/**
 * Type guard for ISimpleFillSymbol
 * @param symbol
 */
export function isSFS(symbol) {
    return symbol.type === RESTSymbolType.SFS;
}
/**
 * Chart types that support rotation.
 */
export const rotationSupportedChartTypes = [
    WebChartTypes.BarSeries,
    WebChartTypes.LineSeries,
    WebChartTypes.ComboLineAndBarSeries,
    WebChartTypes.BoxPlotSeries
];
/**
 * Check if the chart config supports rotation or not
 * @param config - the chart config
 */
export function isRotationSupported(config) {
    const seriesType = getSeriesType(config?.series);
    return seriesType !== undefined ? rotationSupportedChartTypes.includes(seriesType) : false;
}
/**
 * Type guard for ISimpleLineSymbol
 * @param symbol
 */
export function isSLS(symbol) {
    return symbol.type === RESTSymbolType.SLS;
}
/**
 * Type guard for ISimpleMarkerSymbol
 * @param symbol
 */
export function isSMS(symbol) {
    return symbol.type === RESTSymbolType.SMS;
}
/**
 * Type guard for `WebChart` given `WebChart | WebMapWebChart`.
 *
 * Determines if the provided `config` is a `WebChart` based on whether it has a `dataSource`.
 * @param config
 */
export function hasDataSource(config) {
    return config !== undefined && "dataSource" in config && config.dataSource !== undefined;
}
/**
 * Type guard for `WebChartLayerDataSource` given `WebChartLayerDataSource | WebChartInlineDataSource`.
 *
 * Determines if the provided `dataSource` is a `WebChartLayerDataSource`.
 * @param dataSource
 */
export function isFLDataSource(dataSource) {
    return dataSource?.type === WebChartDataSourceTypes.Layer;
}
/**
 * Determines if the provided `config` has a `dataSource` of type `WebChartLayerDataSource`.
 * @param config
 */
export function hasFLDataSource(config) {
    return hasDataSource(config) && isFLDataSource(config.dataSource);
}
